 .table-responsive{
   
    min-height: 200px;
    max-height: 500px;
    position: relative;
    overflow: auto;
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 1px;
    margin-top: 10px;
    border-radius: 10px;
}
.grid{
    display: grid;
    grid-template-columns: 1fr;
  
    
}
.table-responsive h2{
    margin-bottom: 10px;
}

 .table-responsive table{
    border-collapse: collapse;
    
    
}
.payed{
    background-color: var(--main-color);
}
.pending{
    background-color: var(--red-color);
}
.status{
   
    width: 15px;
    margin-right: 10px;
    border-radius: 10px;
}


thead td{
    font-weight: 600;
    padding: 10px;
}
thead{
    position: sticky;
    top: 0;
    background-color: var(--white);
    background-color: var(--main-color);
    color: white;
    border-bottom:  1px solid black;
    margin-bottom: 10px;
}
tbody td{
    padding: 10px;
    font-size: 12px;
    
}
tbody tr:nth-child(odd){
    background-color: rgba(0, 0, 0, 0.1);
}

tbody tr td:last-child{
    display: flex;
}




@media (max-width:691px) {
    table tbody tr td {
        font-size: 8px;
    }
    table thead tr td {
        font-size: 10px;
    }
}