
.modal .modal-container .modal-body{
    padding: 10px;
    width: 50vw;
  
   
}
.grid-modal-body{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 10px;
    
}
.icon-close{
    cursor: pointer;
}

@media (max-width:1255px) {
    .grid-modal-body{
        grid-template-columns: repeat(1,1fr);
   
    }
}

.between{
    padding: 5px;
    justify-content: space-between;
 display: flex;
    width: 100%;
}
.w-100{
    
    flex-grow: 1;
 
 
}
.close-route{
    font-size: 15px;
    font-weight: bold;
}
.h-20{
    height: 35px;
}