.login{
    position: absolute;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--main-color);
}
.login h2{
    color: white;
}

.singin{
    width: 30%;
    height: 350px;
    border: 1px solid #ccc;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}
.login .wrap-input{
    width: 100%;
    display: flex;

    align-items: flex-end;
    color: white;
    margin: 30px;
    padding-left: 30px;
    padding-right: 30px;
}
.login .wrap-input input[type="text"]::placeholder{
    color: white;
}
.login .wrap-input input[type="password"]::placeholder{
    color: white;
}
.login .wrap-input input[type="text"]:active{
    background-color: red;
}
.login .wrap-input input[type="text"], .login .wrap-input input[type="password"]{
    display: block;
    height: 30px;
    flex-grow: 1;
    background-color: transparent;
    outline: none;
    border: none;
    border-bottom: 1px solid white;
    margin-left: 10px;
   color: white;
    appearance: none;

    -webkit-appearance: none;
    -moz-appearance: none;
 
    padding: 5px;
    
   
   

}