.button{
    display: flex;
    justify-content: space-between;
    padding-left: 10px;
    padding-right: 10px;
    color: var(--white);
    font-size: 12px;
    cursor: pointer;
    margin-bottom: 5px;
    font-weight: 800;
    flex-wrap: nowrap;
    overflow: hidden;

}
.button div span{
    margin-left: 10px;
}
#nav:checked ~ .flex-container nav .button span {
    margin-left: 50px;
    display: none;
  }

  #nav:checked ~ .flex-container nav .button .icon-nav {
    margin-left: 6px;
    font-size: 30px;
    font-size: 20px;
  }

  #nav:checked ~ .flex-container nav .button .icon-wrap{
    display: none;
  }

  .icon-wrap{
    transform: rotateZ(-90deg);
    transition: all 300ms;
  }
  

