.modal-delete{
    width: 25%;
    background-color: white;
 overflow: hidden;
    border-radius: 5px;
    padding-bottom: 10px;
}
.header-modal-delete{
    justify-content: space-between;
    width: 100%;
    background-color: var(--main-color);
    padding: 10px;
    display: flex;
    color: white;
}
.body{
    text-align: center;

}
.grid-buttons{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 30px;

    margin: 20px 30px;
}