.dashboard-container{
    display: grid;
    grid-template-columns:repeat(4,1fr);
    grid-gap: 5px;
   margin-top: 100px;
    flex-wrap: wrap;
 
  
}




.customers{
    display: grid;
    grid-template-columns: 70% auto;
    grid-gap: 10px;
    margin-top: 30px;


}

.customers .card-new{
    background-color: var(--white);
    padding: 10px;
    height: 150px;
}
.customers h2{
    margin-bottom: 15px;
}


@media (max-width:1123px) {
    .dashboard-container{
        grid-template-columns: repeat(2,1fr);
    }
}
@media (max-width:529px) {
    .dashboard-container{
        grid-template-columns: 1fr
    }
   
}