.input-wrap-form{
    height: 100%;
    border: 1px solid #ccc;
    height: 30px;
    border-radius: 5px;
    height: 40px;
 
    
  
}
.input-wrap-form input[type="number"]::-webkit-inner-spin-button{
    appearance: none;
}
    

.input-wrap-form input[type="text"],input[type="number"]{
    border:none;
    outline: none;
    width: 100%;
    height: 100%;
    padding-left: 10px;
    display: block;
    background-color: transparent;
   
    
}
.input-wrap-form input[type="password"]{
    border:none;
    outline: none;
    width: 100%;
    height: 100%;
    padding-left: 10px;
    display: block;
    background-color: transparent;
 
    
}
form label{
 
    display: block;

    
    
}
form .flex{
    display: flex;
    align-items: center;
    margin-bottom: 8px;
   
}
form .flex span{
    color: red;
    margin-top: 10px;
    font-size: 20px;
    margin-left: 10px;
}

@media (max-width:810px) {
    form label{
     font-size: 12px;
        
    }
    .input-wrap-form input[type="text"]{
        font-size: 12px;
    }
}
