.alert{
    padding: 25px;
    border-radius: 10px;
    background-color: rgb(182, 21, 21);
    color:white;
    border: 5px solid white ;
    animation: alert 2s linear;
    font-size: 20px;
    font-weight: bold;
    overflow: hidden;
   
}
.alert-container{
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}





    
