.card{

    padding: 2rem;
    background: #fff;
    display: flex;
    overflow: hidden;
    
    justify-content: space-between;
    border-radius: 5px;
    align-items: center;
    box-shadow: 1px 1px 1px 1px #ccc;
  
    
}
.card h2{
    font-size: 30px;
}
.icon-card{
    font-size: 50px;
    color: var(--main-color);
}



