.modal-contizacion{
 background-color: white ;
 width: 70%; 
 height: 60%;
border-radius: 10px;
 animation:  modal 300ms linear;
 overflow: hidden;

}
.body-cotizacion{
    display: grid;
    grid-template-columns: 1fr;
    padding:10px ;
}
.modal-contizacion .header-modal{
    display: flex;
    padding: 10px;
 justify-content: space-between;
 align-items: center;
    background-color: var(--main-color);
    color: white;
}
.search{
position: absolute;
width: 50%;

background-color: red;
}
.relative-container{
 margin: 10px;
   
    
}
.grid-cotizacion{
    display: grid;
    grid-template-columns: 30% 50%
}
.info-cotizacion{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.table-absolute{
    position: absolute;
   grid-template-columns: repeat(1,1fr);
    height: 260px;
    width: 600px;
    bottom: 0;
    left: 0;
    top: 50px;
    z-index: 9595954;
    display: grid;
    background-color: red;
    box-shadow: 1px 1px 1px s;
}
.second-grid{
    display: grid;
    grid-template-columns: repeat(1,1fr);
    grid-gap: 10px;
    margin-top: 50px;
    
}
.cotizacion{
    margin-left: 10px;
    margin-bottom: 10px;
}
.mt-5{
    margin-top: 20px;
}
.d-flex-cotizacion{
    display: flex;
    align-items: center;
    
}
.end{
 align-self: flex-end;   
}
.contaier-table{
    min-height: 300PX;
}