.modal-list-prod{
    position: absolute;
    top: 100px;
    width: 70%;
    background-color: white;
    box-shadow: 0px 0px 0px 2px whitesmoke;
    border-radius: 5px;
    overflow: hidden;
    animation:  modal 200ms linear;
    min-height: 400px;
    max-height: 400px;
}
.modal-list-prod .header-modal{
    display: flex;
    justify-content: space-between;
    padding: 10px;
    background-color: var(--main-color);
    color: white;
}
.modal-list-container{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
}
.modal-list{
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 10px;
}

@keyframes modal{
    from{
        transform: translateY(-300px);
    }to{
        transform: translateY(0); 
    }
}
