*{
  margin: 0;
  padding: 0;
  box-sizing:border-box;
  font-family: Arial, Helvetica, sans-serif;
  list-style: none;
  user-select: none;
  text-decoration: none;
 
}
:root{
  --white:white;
  --main-color:#3c8dbc;
  --red-color:#6d0000;
}


.pointer{
  cursor: pointer;
}
.btn:hover{
  background-color: rgba(0, 0, 0, 0.8);
}
@keyframes modal{
  from{
      transform: translateY(-300px);
  }to{
      transform: translateY(0); 
  }
}

.modal{
  position: absolute;
  top: 0;
  background-color: rgba(0, 0, 0, 0.3);
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  z-index: 99999;
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal .modal-container{
  background-color: var(--white);
  max-height: 70vh;
  overflow-y: auto;
  animation: modal 200ms linear;
  position: relative;
  box-shadow: 1px 1px 1px 3px whitesmoke;
  border-radius: 8px;
 
}
.modal .modal-container .header-modal{
  background-color: var(--main-color);
  display: flex;
  color: white;
  justify-content: space-between;
  padding: 10px;
  position: sticky;
  top: 0;
  z-index: 99;

}
.sobre{
  z-index: 999;
}
.h-2{
  height: 25px;
}
.input-container{
  width: 200px;
  border: 1px solid #ccc;
  display: flex;
  align-items: center;
  padding: 10px;
  border-radius: 5px;
  margin: 10px;
}
.flex-button{
  display: flex;
  justify-content: flex-end;
}
.input-container input[type="search"]{
  outline:none;
  border: none;
  width: 100%;
  height: 100%;
  padding-left: 5px;
}
.mb-5{
  margin-bottom: 10px;
}
.container{
  width: 100%;
  height: 90vh;
  background-color: var(--white);
  margin-top: 5px;
  padding: 30px;
  border:  1px solid #ccc;
}
.flex-btn{
  display: flex;
  margin-top: 10px;
  justify-content: flex-end;
}
.btn{
  background-color:var(--main-color) ;
  padding: 10px;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
}



a{
  color: var(--main-color);
}

body{
  background-color: #ededed;
}

#nav{
  display: none;
}
#nav:checked ~ .flex-container nav{
  width: 70px;
}
#nav:checked ~ .flex-container .fake-nav{
  width: 70px;
}
#nav:checked ~ .flex-container nav .wrap-menu{
  margin-left: 100px;
}

#wrap_menu{
  display: none;
}
#wrap_menu:checked ~ .wrap-menu {
  height: 130px;
  margin-bottom: 5px;
 
}
#wrap_menu:checked + label .button .icon-wrap {
 
 
  transform: rotateZ(-180deg);
 
}

#wrap_menu-inventario{
  display: none;
}
#wrap_menu-inventario:checked ~ .h-inventory{
  height: 180px;
}

#wrap_menu-inventario:checked + label .button .icon-wrap{
  transform: rotateZ(-180deg);
}

#wrap_menu_control_caja{
  display: none;
}
#wrap_menu_control_caja:checked + label .button .icon-wrap{
  transform: rotateZ(-180deg);
}

.h-control-caja{
  height: 300px;
}
#wrap_menu_control_caja:checked ~ .h-control-caja{
  height: 90px;
}

#wrap_menu_request_pay:checked + label .button .icon-wrap{
  transform: rotateZ(-180deg);
}


#wrap_menu_request_pay{
  display: none;
}
#wrap_menu_request_pay:checked ~ .h-request-pay{
  height: 175px;
}

#wrap_menu_request_cont{
  display: none;
}
#wrap_menu_request_cont:checked + label .button .icon-wrap{
  transform: rotateZ(-180deg);
}
#wrap_menu_request_cont:checked ~.h-request-cont{
  height: 120px;
}
#wrap_menu_vendor{
  display: none;
}
#wrap_menu_vendor:checked + label .button .icon-wrap{
  transform: rotateZ(-180deg);
}

#wrap_menu_vendor:checked ~ .h-vendor{
  height: 100px;
}
.fake-nav{
  width: 350px;

  transition: all 300ms;
}
.flex-container nav{
  width: 350px;
  position: fixed;
  bottom: 0;
  top: 0;

  z-index: 999;
  background-color:var(--main-color);
  transition: all 300ms;
  display: block;

  overflow: hidden;
}
.wrap-menu{
  color: white;
  margin-left: 40px;
  height: 0px;
  overflow: hidden;
  margin-bottom: 10px;
  transition: all 300ms;
}

.wrap-menu ul li a{
  color: white;
  font-size: 12px;
  margin-top: 5px;
  margin-bottom: 5px;
  display: block;
}
.flex-container main{
  flex: 1;

  margin-left: 10px;
  margin-right: 10px;
  transition: all 200ms;


}
.flex-container{
  display: flex;

  min-height: 100vh;
}
.brand img{
 border-radius: 5px;
  background-color: var(--white);
  padding: 3px;
  width: 50px;
  height: 50px;
  margin-right: 20px;


}
.brand{
  height: 10%;


  padding: 10px;
  display: flex;
  color: white;

  align-items: center;
}
#navMenuMobile{
  display: none;
}
#navMenuMobile:checked ~ .flex-container nav{
  transform: translateX(0px);
}


@media (max-width:1275px) {
  .flex-container nav{
    width: 350px !important;
  }
  #nav:checked ~ .flex-container .fake-nav{
    width: 0px;
  }
 .flex-container main .content{
    margin-left: 0px;
    margin-right: 0px;
    
  }
  .fake-nav{
    width: 0px;
  }
  .flex-container main{

    margin-left: 0px;
    margin-right: 0px;
  
  }
  .flex-container nav{
    position: absolute;
    margin-top: 50px;
    transform: translateX(-400px);
    
    
  }
  #navMobile{
    display: none;
  }
  #menuMobile{
    display: none;
  }

  #nav:checked ~ .flex-container main{
    margin-left: 0px;
   }
  #nav:checked ~ .flex-container nav{
    transform: translateX(0px);
  }


 

  

}

.underline{
  text-decoration: underline;
}

select{
  width: 100%;
   height: 30px;
   padding-left: 5px;
   outline: none;
   border: 1px solid #ccc;
   position: relative;
   
   
}