.modal .modal-register-prod{
    width:60% ;
    background-color: white;
    padding-bottom: 10px;
    animation:  modal 300ms linear;
    border-radius: 10px;
    overflow: hidden;
}
.modal .modal-register-prod .header-modal{
    background-color: var(--main-color);
    display: flex;
    padding: 10px;
    justify-content: space-between;
    color: white;
    position: sticky;
}
.grid-prod{
    padding: 10px;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 10px;
}
.pl{
    padding-right: 10px;
}
.date{
    margin-top: 10px;
}
.date input[type="date"]{
    display: block;
    padding: 5px;
    width: 100%;
    margin-top: 5px;
    border: 1px solid #ccc;
    outline: none;
    
}

@media (max-width:1272px) {
    .grid-prod{
        grid-template-columns: repeat(2,1fr);
    }
}
@media (max-width:810px) {
        .grid-prod{
            grid-template-columns: repeat(1,1fr);
        }
         h3{
            font-size: 15px;
        }
        .modal .modal-register-prod{
            width: 90%;
        }
}