.loading {
    width: 90px;
    height: 90px;
    border-radius: 50%;
    border: 5px solid white;
    border-top: 5px solid var(--main-color);
    animation: spin 1s linear infinite;
    position: absolute;
    
    
    
    
    
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
  