
.clear{
    height: 50px;
}


.data-client{
    display: grid;
    grid-gap: 10px;
    grid-template-columns: repeat(4,1fr);
    padding: 5px;
    font-size: 12px;
    margin-top: 5px;
}
 .fake-input{
    background-color:var(--main-color);
    color: white;
    font-weight: bold;
    
    padding: 6px;
    text-transform: uppercase;
}
.data-client div label{
    font-size: 14px;
    margin-bottom: 5px;
    display: block;
}
.grid-fact{
    display: grid;
    grid-template-columns: 70% 30%;
    grid-gap: 10px;
    flex-wrap: wrap;
    min-height: 300px;
    max-height: 300px;
    border-bottom: 1px solid #ccc;
    margin: 0px 10px;
    
    
    
}
.total{
    text-align: left;
}
.total span{
    font-weight: bold;
    
}
.total p{
    
    font-size: 10px;
    margin-top: 4px;
}

.m-5{
   margin-right: 10px;
}
.price-and-total{
    display: grid;
    flex-wrap: wrap;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 10px;
    margin-top: 10px;
    padding: 15px;
}
.flex-between{
    display: flex;
    justify-content: space-between;
    align-items:flex-end;
  
}
.grid-fact h4{
    margin-top: 10px;
}
.grid-price{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    margin-top: 30px;
    grid-gap: 12px;
    margin-right: 10px;
}
.flex{
    display: flex;
    align-items: flex-end;
}

.flex-total{
    display: grid;
   grid-template-columns: repeat(3,1fr);
    padding: 10px;
    
  
}
.left-total{
    display: grid;
    grid-template-columns: repeat(2,1fr);
}
.flex-total .left-total div{
    margin-top: 5px;
    
}
.size{
    font-size: 12px;
    margin-left: 10px;
}
.flex-between-price{
    display: flex;
    align-items: center;
    
}

.condition{
    width: 100%;
}

.flex-total .right .condition section{
    width: 300px !important
    
}
.d-flex{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 6px;
}
.header-details{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.w-25{
   width: 35%;

   display: block; 
}
.ml-10{
    margin-right: 100px;
    
}

.mb-2{
    margin-bottom: 5px;
    display: block;
}

@media (max-width:1222px) {
    .modal .modal-container{
        overflow-y: scroll;
       width: 99%;
      
      
    }
    .btn{
        font-size: 12px;
    }
    .flex-total{
        grid-template-columns: 60% 20%;
        grid-gap: 10px;
    }
    .right{
        grid-row: 1;
    }
    .center{
        grid-row: 3;
    }
    .ml-10{
        margin-top: 30px;
    }
    
}
@media (max-width:923px) {

 .flex-total{
    grid-template-columns: 1fr;
 }
 .data-client{
    grid-template-columns: repeat(2,1fr);
   
 } 
 .grid-fact{
    grid-template-columns: 1fr;
 }
 .grid-responsive{
    display: grid;
    grid-template-columns: repeat(2,1fr);
  

 }

 .title{
    font-size: 10px;
    
    word-wrap: break-word;
 }
 .flex-total{
    margin-top: 100px;
 }
 .d-flex{
    justify-content: space-between;
 }
 .btn{
    font-size: 8px;
}
 
}