header{

    background-color:var(--white);
    
height: 60px;
    position: sticky;
  
    top: 0;
    left: 0;
    width: 100%;

    display: flex;
    padding-left: 10px;
    padding-right: 20px;
    align-items: center;
    justify-content: space-between;
    transition: all 200ms;
   border: 1px solid #ccc;
    

}
header .left{
    display: flex;
    align-items: center;
}
header .left h1{
    margin-left: 14px;
    font-size: 20px;
}
.menu-btn{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 30px;    
    width: 30px;
    cursor: pointer;
}
.menu-btn-2{
    display: none;
    flex-direction: column;
    justify-content: space-evenly;
    height: 30px;    
    width: 30px;
    cursor: pointer;
}
.menu-btn-2 .line{
    height: 2px;
    background-color: var(--main-color);
}

.menu-btn .line{
    height: 2px;
    background-color: var(--main-color);
   
}
header .right small{
    font-size: 10px;
}
header .right img{
    width: 20px;
    height: 20px;
    cursor: pointer;
}
header .right .user{
    display: flex;
    align-items: center;
    margin-right: 20px;
    

}
header .right .user p{
    margin: 0px 10px;
    font-size: 12px;
}
header .right{
   
    display: flex;
    align-items: center;
   
    justify-content: space-between;
}


.menu li {
    position: relative;
    list-style: none;
    
  
}
.icon-user{
    font-size: 20px;
}
#menu_user{
    display: none;
}
#menu_user:checked + ul{
    display: block;
}
.menu li ul{
    
    position: absolute;
    display: none;
    top: 40px;
    transition: all 0.3s;
    left: -168px;
    color: black;
    background-color: white;
    box-shadow: 2px 2px 2px 2px #ccc;
    

}


.menu li ul li{
    text-align: center;
    cursor: pointer;
    font-size: 12px;
    margin: 5px 0px;
    width: 200px;
    padding: 10px;

}
.menu li ul li:hover{
  display: block;
    padding: 10px;
    background-color: rgba(255, 255, 255, 0.5);
}
.user-icon{
    margin-right: 20px;
}




@media (max-width:1275px) {


 
    .menu-btn-2{
        display: flex;
    }
    .menu-btn{
        display: none;
    }
}

@media (max-width:534px) {
    header .left h1{
        font-size: 12px;
    }
    header .right h4{
        font-size: 12px;
    }
    header .right small{
        font-size: 10px;
    }

    .menu-btn-2{
     
        height: 20px;    
        width: 20px;
    
    }
}