form .input-search {
    display: flex;
   align-items: center;
   width: 300px

    
}

form .input-search .input-wrap input[type="search"]{
    outline: none;
    border: none;
    height: 100%;
    width: 100%;
    padding-left: 5px;
 
   display: block;
}
form .input-search .input-wrap {
    display: flex;
    border: 1px solid #ccc;
    padding: 10px;
    flex-grow: 1;
    border-radius: 10px;
}
.wrap-button {
    display: flex;
    background-color:var(--main-color);
    height: 30px;
    justify-content: center;
    align-items: center;
    padding: 5px;
    width: 80px;
    cursor: pointer;
    border-radius: 5px;
    margin-left: 30px;

}
.icon{
    color: white;
    font-size: 12px;
}
.wrap-button .btn{
    border: none;
    outline: none;
    background-color: transparent;
    margin-left: 5px;
    text-align: left;
    color: white;
    font-size: 12px;
 
}